.Logo{
  width: 100px;
  height: 50px;
  }

.Burger {
display: inline;
}
.button-close{
  color: white;
  height: 2.5rem;
}

  
  @media (min-width: 767px) {
    .Logo{
      width: 100px;
      height: 50px;
      }
      .button-close{
          color: white;
          height: 2.5rem;
      }
      .Burger {
        display: none;
        grid-area: burger;
        margin: 0 40px 0 0;
        padding: 0;
        justify-self: end;
        font-size: 40px;
        border: none;
        background: none;
        outline: none;
        transition: 0.1s;
      }
      .Burger:active {
        transform: scale(1.2);
      }
      
   
  }
  


  .NavAnimation-enter {
    opacity: 0;
    transform: scale(0.5);
  }
  .NavAnimation-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 350ms, transform 350ms;
  }
  .NavAnimation-exit {
    opacity: 1;
  }
  .NavAnimation-exit-active {
    opacity: 0;
    transform: scale(0.5);
    transition: opacity 350ms, transform 350ms;
  }
